// GET - List invoices
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    
    // Query parameters
    const subAccountId = searchParams.get('subAccountId')
    const search = searchParams.get('search') || ''
    const status = searchParams.get('status')
    const page = parseInt(searchParams.get('page') || '1')
    const limit = parseInt(searchParams.get('limit') || '50')
    
    // Validation
    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }
    
    // Return mock data for now (database tables not yet created)
    let invoices = getMockInvoices().filter(i => i.subAccountId === subAccountId || subAccountId === 'default-sub-account')
    
    // Apply search filter
    if (search) {
      const searchLower = search.toLowerCase()
      invoices = invoices.filter(i => 
        i.invoiceNumber.toLowerCase().includes(searchLower) ||
        i.contactName.toLowerCase().includes(searchLower) ||
        i.contactEmail.toLowerCase().includes(searchLower)
      )
    }
    
    // Apply status filter (only if status is a valid value, not 'undefined' string)
    if (status && status !== 'undefined' && status !== 'all') {
      invoices = invoices.filter(i => i.status === status)
    }
    
    // Calculate summary stats
    const allInvoices = getMockInvoices()
    const summary = {
      total: allInvoices.length,
      draft: allInvoices.filter(i => i.status === 'draft').length,
      sent: allInvoices.filter(i => i.status === 'sent').length,
      paid: allInvoices.filter(i => i.status === 'paid').length,
      overdue: allInvoices.filter(i => i.status === 'overdue').length,
      totalAmount: allInvoices.reduce((sum, i) => sum + i.totalAmount, 0),
      totalPaid: allInvoices.reduce((sum, i) => sum + i.amountPaid, 0),
      outstanding: allInvoices.reduce((sum, i) => sum + i.amountDue, 0),
    }
    
    return NextResponse.json({
      success: true,
      data: {
        invoices,
        summary,
        pagination: {
          page,
          limit,
          total: invoices.length,
          totalPages: Math.ceil(invoices.length / limit),
        },
      },
    })
  } catch (error: any) {
    console.error('GET /api/v1/revenue-engine/invoices error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to fetch invoices' },
      { status: 500 }
    )
  }
}

// POST - Create new invoice (mock implementation)
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const {
      subAccountId,
      contactId,
      contactName,
      contactEmail,
      issueDate,
      dueDate,
      items,
      taxRate,
      discountType,
      discountValue,
      notes,
    } = body
    
    // Validation
    if (!subAccountId || !contactName || !contactEmail || !issueDate || !dueDate) {
      return NextResponse.json(
        {
          success: false,
          error: 'subAccountId, contactName, contactEmail, issueDate, and dueDate are required',
        },
        { status: 400 }
      )
    }
    
    if (!items || !Array.isArray(items) || items.length === 0) {
      return NextResponse.json(
        { success: false, error: 'At least one item is required' },
        { status: 400 }
      )
    }
    
    // Generate invoice number
    const year = new Date().getFullYear()
    const invoiceNumber = `INV-${year}-${String(Date.now()).slice(-5)}`
    
    // Calculate totals
    let subtotal = 0
    const processedItems = items.map((item: any, index: number) => {
      const quantity = parseFloat(item.quantity) || 1
      const unitPrice = parseFloat(item.unitPrice) || 0
      const lineTotal = quantity * unitPrice
      subtotal += lineTotal
      
      return {
        id: `item-${Date.now()}-${index}`,
        name: item.name,
        description: item.description || null,
        quantity,
        unitPrice,
        totalAmount: lineTotal,
      }
    })
    
    // Calculate invoice totals
    const invoiceTaxRate = parseFloat(taxRate) || 0
    const taxAmount = subtotal * (invoiceTaxRate / 100)
    
    let discountAmount = 0
    if (discountType === 'percentage' && discountValue) {
      discountAmount = subtotal * (parseFloat(discountValue) / 100)
    } else if (discountType === 'fixed' && discountValue) {
      discountAmount = parseFloat(discountValue)
    }
    
    const totalAmount = subtotal + taxAmount - discountAmount
    
    // Create mock invoice
    const invoice = {
      id: `inv-${Date.now()}`,
      invoiceNumber,
      subAccountId,
      contactId: contactId || null,
      contactName,
      contactEmail,
      status: 'draft',
      issueDate,
      dueDate,
      subtotal,
      taxAmount,
      discountAmount,
      totalAmount,
      amountPaid: 0,
      amountDue: totalAmount,
      currency: 'USD',
      items: processedItems,
      notes: notes || null,
      createdAt: new Date().toISOString(),
    }
    
    return NextResponse.json({
      success: true,
      data: { invoice },
      message: 'Invoice created successfully (mock data - database tables not yet created)',
    }, { status: 201 })
  } catch (error: any) {
    console.error('POST /api/v1/revenue-engine/invoices error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to create invoice' },
      { status: 500 }
    )
  }
}
